import { NavBar, Icon } from 'antd-mobile';
import React from 'react';
import { Carousel, WingBlank,WhiteSpace,Tabs } from 'antd-mobile';
import "./index.scss";
import phoneIcon from './images/phone.png';
import pcIcon from './images/PC.png';
import collectionIcon from './images/collection.png';
import trainingIcon from './images/dailian.png'
import GoodsItemComponent from '../../Components/Goods-Item';
type GameItem = {
  name: string;
  img: string;
  url?: string;
}
type goodsItem = {
  platform: string;
  desc: string; // 账号描述
  isBindId?: boolean;
  type: string // phone手机账号 email邮箱账号
  price?: number; //账号价格
  src?: string;
}
interface Props {}
interface State {
  banners: Array<any>
  imgHeight: number | string;
  tabs: Array<any>;
  phoneGames: Array<GameItem>;
  goodsList: Array<goodsItem>;
}

class HomePage extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      banners: ['1', '2', '3'],
      imgHeight: 176,
      tabs: [
        { title: '热门手游' },
        { title: '热门端游' },
      ],
      phoneGames:[
        {
          name: '梦幻西游',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/5/22/bb712d01-6c11-4376-b813-e8e57ba11c14.jpg'
        },
        {
          name: '王者荣耀',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/6/12/8b3b643b-70a1-4204-b0d1-3ca056c86373.jpg'
        },
        {
          name: '和平精英',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/6/12/337fb0b4-3c44-4532-90f7-58fbb95361ef.jpg'
        },
        {
          name: '和平精英',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/6/12/337fb0b4-3c44-4532-90f7-58fbb95361ef.jpg'
        },
        {
          name: '梦幻西游',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/5/22/bb712d01-6c11-4376-b813-e8e57ba11c14.jpg'
        },
        {
          name: '王者荣耀',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/6/12/8b3b643b-70a1-4204-b0d1-3ca056c86373.jpg'
        },
        {
          name: '和平精英',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/6/12/337fb0b4-3c44-4532-90f7-58fbb95361ef.jpg'
        },
        {
          name: '和平精英',
          img: 'https://static.jiaoyimao.com/resource/public/game/2020/6/12/337fb0b4-3c44-4532-90f7-58fbb95361ef.jpg'
        },
      ],
      goodsList: [
        {
          platform: '安卓平台',
          desc: '[89级阴曹地府 男]76678非测试区89男地府',
          isBindId: false, // 是否绑定身份证
          type: 'phone', //手机账号
          price: 21500,
          src: 'https://static-cdn.jiaoyimao.com/jym-oss/resource/item/1445168108811478/20210601/215900432.jpeg?x-oss-process=image/resize,h_300,w_300,m_mfit,g_north/quality,Q_80/format,jpg'
        },
        {
          platform: '安卓平台',
          desc: '[89级阴曹地府 男]76678非测试区89男地府',
          isBindId: false, // 是否绑定身份证
          type: 'phone', //手机账号
          price: 21500,
          // src: '222'
          src: 'https://static-cdn.jiaoyimao.com/jym-oss/resource/item/1445168108811478/20210601/215900432.jpeg?x-oss-process=image/resize,h_300,w_300,m_mfit,g_north/quality,Q_80/format,jpg'
        },
      ]
    }
  }

  render() {
    return (
      <div>
    <NavBar
      mode="light"
      title="梦幻手游交易平台"
      icon={<Icon type="left" />}
      onLeftClick={() => console.log('onLeftClick')}
      rightContent={[
        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
        <Icon key="1" type="ellipsis" />,
      ]}
    ></NavBar>
    <div className="banners_wrap">
      {this.renderBanner()}
    </div>
    <div className="business_outer_wrap">
      <ul className="business_outer_ul">
        <li className="business_outer_li">
          <div className="img_wrap b1">
            <img src={phoneIcon}></img>
          </div>
          <span>手游</span>
        </li>
        <li className="business_outer_li">
          <div className="img_wrap b2">
            <img src={pcIcon}></img>
          </div>
          <span>端游</span>
        </li>
        <li className="business_outer_li">
          <div className="img_wrap b3">
            <img src={collectionIcon}></img>
          </div>
          <span>收藏</span>
        </li>
        <li className="business_outer_li">
          <div className="img_wrap b4">
            <img src={trainingIcon}></img>
          </div>
          <span>代练</span>
        </li>
      </ul>
    </div>
    <WhiteSpace size="md" />
    <div className="notice_bar_wrap">
      <span className="notice_bar_title">最新成交</span>
      {this.renderNoticeBar()}
    </div>
    <WhiteSpace size="md" />
    <div className="hot_games_wrap">
      {this.renderTabs()}
    </div>
    <div className="advertisement_wrap">
      {this.renderAdBanner()}
      <div style={{textAlign: 'right',color: '#999'}}>广告区</div>
    </div>
    <div className="recommend_wrap">
      {this.state.goodsList.map((goods: any) => {
        return <GoodsItemComponent goods={goods}  />
      })}
      <div style={{clear: 'both'}}></div>
    </div>
  </div>
    )
  }

  private renderBanner() {
    return (
      <WingBlank>
        <Carousel
          autoplay={false}
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.banners.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`https://api.kangehao.com/static/images/loop/d8067378964f2c59b6a5ec8300c54823.jpeg`}
                alt=""
                style={{ width: '100%', height: this.state.imgHeight, verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }

  /**
   * 广告banner
   * @returns 
   */
  private renderAdBanner() {
    return (
      <WingBlank>
        <Carousel
          className="ad_carousel_wrap"
          autoplay={false}
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}
        >
          {this.state.banners.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: '100px', borderRadius: '6px' }}
            >
              <img
                src={`https://img.alicdn.com/imgextra/i1/O1CN01ETzlJ721eAiUrACyu_!!6000000007009-2-tps-690-260.png`}
                alt=""
                style={{ width: '100%', height: this.state.imgHeight, verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }

  private renderNoticeBar() {
    return (

      // <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }} mode="link" icon={<span>最新订单</span>} onClick={() => alert('1')}>
        <Carousel className="notice_carousel"
          vertical
          dots={false}
          autoplay
          infinite
        >
          <div className="v-item">孙悟空 购买了 4464601金988钻 </div>      
          <div className="v-item">张文 购买了 梦幻西游账号 115级 神威大唐 </div>      
          <div className="v-item">鑫哥 购买了 梦幻西游账号 115级 神威魔王 </div>   
        </Carousel>
      // </NoticeBar>
    )
  }

  private renderTabs() {
    return (
      <Tabs tabs={this.state.tabs} initialPage={0} tabBarTextStyle={{color: 'rgba(191, 191, 191, 1)'}} tabBarActiveTextColor={'#333'} animated={true} useOnPan={false}>
      <div className="game_ul">
        {
          this.state.phoneGames.map((phoneGame, index: number) => {
            return this.renderSingleGame(phoneGame);
          })
        }
        <div style={{clear: 'both'}}></div>
      </div>
      <div className="game_ul">
        {
          this.state.phoneGames.map((phoneGame, index: number) => {
            return this.renderSingleGame(phoneGame);
          })
        }
        <div style={{clear: 'both'}}></div>
      </div>
    </Tabs>
    )
  }

  private renderSingleGame(phoneGame: GameItem) {
    return (
      <div className="game_item">
        <img src={phoneGame.img} alt="游戏名" />
        <span className="game_name">{phoneGame.name}</span>
      </div>
    )
  }
}

export default HomePage