import * as React from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import HomePage from "../Pages/Home";
import GoodsStack from "./GoodsStack";

class AppRouter extends React.Component {
  render() {
    return (
      <div className="app">
        <Router>
          <Switch>
            <Route path="/home">
              <HomePage />
            </Route>
            <Route path="/goods">
              <GoodsStack />
            </Route>
          </Switch>
        </Router>
      </div>
    )
  }
}

export default AppRouter