import React from 'react';
import "./index.scss";

interface Props {
  src?: string;
  defaultSrc?: string;
}
interface State {
  loaded?: boolean;
}

class ImagePreview extends React.Component<Props, State> {

  static defaultProps: Props = {
    src: 'https://static-cdn.jiaoyimao.com/jym-oss/resource/item/1445168108811478/20210601/215900432.jpeg?x-oss-process=image/resize,h_300,w_300,m_mfit,g_north/quality,Q_80/format,jpg',
  }
  
  constructor(props: Props) {
    super(props);
    this.state = {
      loaded: false
    }
  }

  render() {
    return (
      <div className="image_preview_wrap">
        {this.state.loaded ? null : (<div className="image_default">
          <span>图片加载中...</span>
        </div>)}
        <img className="image_real" 
             style={{opacity: this.state.loaded ? 1 : 0}} 
             src={this.props.src} 
             onLoad={(e) => this.handleSuccessLoad(e)} 
             onError={(e) => this.handleFailLoad(e)}
             alt="图片" />
      </div>
    )
  }

  private handleSuccessLoad(e: any): void {
    this.setState({
      loaded: true
    })
  }

  private handleFailLoad(e: any): void {
    this.setState({
      loaded: true
    })
  }
}

export default ImagePreview