import React from 'react';
import ImagePreview from '../Preview-Image';
import "./index.scss"

type goodsItem = {
  platform?: string;
  desc?: string; // 账号描述
  isBindId?: boolean;
  type?: string // phone手机账号 email邮箱账号
  price?: number; //账号价格
  src?: string;
}

interface Props {
  layout?: string;
  src?: string;
  goods: goodsItem
}

interface State {
}

class GoodsItemComponent extends React.Component<Props, State> {
  static defaultProps: Props = {
    layout: 'pic',
    goods: {
    }
  }
  constructor(props: Props) {
    super(props);
    this.state = {
    }
  }

  render() {
    return this.props.layout == 'pic' ? this.renderPicLayout(this.props.goods) : this.renderNoPicLayout();
  }

  /**
   * 有图浏览模式
   * @returns 
   */
  renderPicLayout(goods: goodsItem) {
    return (
      <div className="goods_item_wrap has_pic">
        <div className="item_top">
          <ImagePreview src={goods.src}></ImagePreview>
        </div>
        <div className="item_middle">
          <div className="desc_group">
            <span className="tags">
              账号
            </span>
            <span className="desc">
              【69级大唐官府.剑侠客】一线大唐，弃坑
            </span>
          </div>
          <div className="mid_group">
            <span className="goods_type">
              手机账号
            </span>
            <span className="goods_extra">
              花好月圆
            </span>
          </div>
        </div>
        <div className="item_bottom">
          <div className="price_wrap">
          <span className="price_label">￥</span>
          <span className="price_value">21500</span>
          </div>
          <span className="stars">
            119人想要
          </span>
        </div>
    </div>
    )
  }
  
  /**
   * 无图浏览模式
   * @returns 
   */
  renderNoPicLayout() {
    return (
      <div className="goods_item_wrap no_pic">
        <div className="item_top">
        </div>
        <div className="item_middle"></div>
        <div className="item_bottom"></div>
    </div>
    )
  }
}

export default GoodsItemComponent